.search {
   &-form {
      & + & {
         margin-top: 10px;
      }
      &._flex {
         .search-form-inner {
            display: flex;
            align-items: center;
            .search-label {
               flex-grow: 1;
               display: flex;
               align-items: center;
            }
            .search-input {
               flex-grow: 1;
            }
            .search-btn {
               flex-shrink: 0;
            }
         }
      }
      &._grid {
         .search-form-inner {
            display: grid;
            grid-template-columns: 1fr auto;
            grid-template-areas: "search";
            .search-label {
               grid-area: "search" "btn";
            }
            .search-input {
               width: 100%;
            }
            .search-btn {
               grid-area: "btn";
            }
         }
      }
      &._table {
         display: table;

         .search-form-inner {
            display: table-row;
            .search-label {
               display: table-cell;
               vertical-align: middle;
               width: 100%;
            }
            .search-input {
               width: 100%;
            }
            .search-btn {
               display: table-cell;
               vertical-align: middle;
            }
         }
      }
   }
   &-input {
      width: 0;
      height: 38px;
      padding: 0 37px 0 35px;
      background: #f2f2f2;
      border: 1px solid #fff;
      box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1), inset 0px -1px 2px rgba(0, 0, 0, 0.6),
         inset 0px 2px 3px rgba(0, 0, 0, 0.6);
      border-radius: 3px;
      transition: all 0.1s;

      font-family: $roboto;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #373f45;

      &:focus {
         background: #fff;
      }

      &::-webkit-search-cancel-button {
         -webkit-appearance: none;
         width: 17px;
         height: 18px;
         background: url("/assets/icons/cross-search.png") no-repeat center center;
         background-size: contain;
         cursor: pointer;
         transform: translate(29px);
         transition: all 0.1s;
      }
   }
   &-label {
      position: relative;
      &::after {
         content: "";
         position: absolute;
         background: url("/assets/icons/search.png") no-repeat center center;
         background-size: contain;
         top: 0;
         bottom: 0;
         margin: auto;
         left: 11px;
         width: 16px;
         height: 16px;
         cursor: pointer;
         z-index: 1;
      }
   }
   &-btn {
      position: relative;
      z-index: 5;
      margin-left: 10px;
      padding: 1px 15px 0;

      height: 38px;
      transition: all 0.1s;

      font-family: $roboto;
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: #373f45;

      &::before {
         content: "";
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         cursor: pointer;
         transition: all 0.1s;
         background: linear-gradient(180deg, rgba(177, 181, 183, 0) 0%, rgba(177, 181, 183, 0.25) 100%), #e5e5e5;
         box-shadow: -1px 0px 1px rgba(0, 0, 0, 0.25), 1px 0px 1px rgba(0, 0, 0, 0.25), 0px 1px 1px rgba(0, 0, 0, 0.4),
            inset 0px 1px 0px rgba(255, 255, 255, 0.5);
         border-radius: 3px;
         z-index: -3;
      }
      &::after {
         content: "";
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         cursor: pointer;
         transition: all 0.2s;
         background: linear-gradient(180deg, rgba(177, 181, 183, 0) 0%, rgba(155, 159, 162, 0.4) 100%), #e5e5e5;
         border-radius: 3px;
         box-shadow: inset 0px 0px 20px 7px rgba(255, 255, 255, 0.4), inset 0px 1px 0px rgba(255, 255, 255, 0.5);
         z-index: -1;
      }
      &:hover {
         &::after {
            opacity: 0;
         }
      }
   }
}


.explanation {
   font-family: $roboto;
   font-weight: 400;
   font-size: 14px;
   line-height: 1.5;
   color: #585858;
   margin-bottom: 10px;
}